/** @format */
import withStyles from "@material-ui/core/styles/withStyles";
import { app } from "assets/jss/app-theme.jsx";
import GridContainer from "components/Grid/GridContainer";
import GridItem from "components/Grid/GridItem";
import React from "react";
import { connect } from "react-redux";
import { withTheme } from '@material-ui/core/styles';
import compose from "recompose/compose";
import { withErrorHandler } from "hoc/ErrorHandler";


// Sections for this page
import AboutUsSection from "views/sections/LandingPage/AboutUs";
import HowToBecomeAfielderSection from "views/sections/LandingPage/HowToBecomeAfielder";
import FAQsSection from "views/sections/LandingPage/FAQs";

const styles = theme => ({
	root: {		
		position: "relative",
		minHeight: "100vh",
	},
	title: {		
		color: theme.palette.text.secondary,
		textDecoration: "none",
	},
	subtitle: {
		margin: "10px auto 0",
	},
});

class Page extends React.Component {
	componentDidMount() {
		document.title = app.title("About Us");
	}

	render() {
		const { classes, auth, theme, device, ...rest } = this.props;
		return (
				<GridContainer
					className={classes.root+" p-0 sm:px-4 md:px-32"}
					direction="column"
					alignItems="center"
					justify="center"
				>
					<GridContainer className={"p-0 px-4"}>
						<GridItem xs={12} sm={12} md={12} className={"p-0"}>
							<AboutUsSection />
						</GridItem>

						<GridItem xs={12} sm={12} md={12}  className={"p-0"}>
							<HowToBecomeAfielderSection />
						</GridItem>		

						<GridItem xs={12} sm={12} md={12}  className={"p-0"}>
							<FAQsSection />
						</GridItem>							
					</GridContainer>
				</GridContainer>
		);
	}
}

const mapStateToProps = (state, ownProps) => ({
	auth: state.auth,
	device: state.device,
});

export default withErrorHandler(
	compose(connect(mapStateToProps, {}), withStyles(styles), withTheme)(Page)
);

